"use client"
import { useState } from "react";
import { Popover, PopoverContent, PopoverTrigger } from "../ui/popover";
import { Button } from "../ui/button";
import { HiMenuAlt1, HiMenuAlt3 } from "react-icons/hi";
import { LuUserSearch, LuBrain, LuCodeXml, LuBriefcase } from "react-icons/lu";
import { setSourceMapsEnabled } from "process";

export default function Menu() {

    const [ showMenu, setShowMenu ] = useState<boolean>(false);

    return (
        <Popover 
            open={ showMenu }
            onOpenChange={(open) => {
                
                if (!open) {
                    setSourceMapsEnabled()
                }
            }}
        >
            <PopoverTrigger asChild>
                <Button>
                    {
                        showMenu ?
                        <HiMenuAlt1 /> :
                        <HiMenuAlt3 /> 
                    }
                </Button>
            </PopoverTrigger>
            <PopoverContent 
                className="grid gap-3" 
                align="end"
            >
                <div>
                    <LuUserSearch className="text-white size-5"/>
                </div>
                <div>
                    <LuBrain className="text-white size-5"/>
                </div>
                <div>
                    <LuBriefcase className="text-white size-5"/>
                </div>
                <div>
                    <LuCodeXml className="text-white size-5"/>
                </div>
            </PopoverContent>
        </Popover>
    )
}